body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
  }
  
  .resume {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
  }
  
  header {
    text-align: center;
    margin-bottom: 20px;
  }
  
  header h1 {
    font-size: 28px;
    margin: 0;
  }
  
  header p {
    font-size: 16px;
    margin: 5px 0;
  }
  
  .personal-statement {
    margin-bottom: 20px;
  }
  
  .personal-statement h2 {
    font-size: 24px;
    margin: 0;
  }
  
  .personal-statement p {
    font-size: 16px;
  }
  
  .work-experience {
    margin-bottom: 20px;
  }
  
  .work-experience h2 {
    font-size: 24px;
    margin: 0;
  }
  
  .experience-item {
    margin-bottom: 20px;
  }
  
  .experience-item h3 {
    font-size: 20px;
    margin: 0;
  }
  
  .experience-item p {
    font-size: 16px;
    margin: 5px 0;
  }
  
  .experience-item ul {
    list-style-type: disc;
    margin-left: 20px;
  }
  
  .experience-item li {
    font-size: 16px;
  }
  
  .education {
    margin-bottom: 20px;
  }
  
  .education h2 {
    font-size: 24px;
    margin: 0;
  }
  
  .education-item {
    margin-bottom: 10px;
  }
  
  .education-item h3 {
    font-size: 20px;
    margin: 0;
  }
  
  .education-item p {
    font-size: 16px;
    margin: 5px 0;
  }
  
  